import axios from 'axios';
import moment from 'moment';
import i18n from '../i18n';

export default {
  namespaced: true,
  state: {
    currentLocale: 'ru',
    LSVersion: '1.0',
    allowedLocales: ['ru', 'en'],
    localeIsReady: false,
  },
  getters: {
    currentLocale: (state) => state.currentLocale,
    allowedLocales: (state) => state.allowedLocales,
    localeIsReady: (state) => state.localeIsReady,
  },
  mutations: {
    setCurrentLocale: (state, locale) => state.currentLocale = locale,
    setLocaleIsReady: (state) => state.localeIsReady = true,
  },
  actions: {
    setLocale: async ({ commit, dispatch }, locale) => {
      if (!locale) {
        locale = await dispatch('getLocaleFromLS', locale) || 'ru';
      }
      if (!i18n.availableLocales.includes(locale)) {
        const translate = await axios(`/langs/${locale}.json`);
        i18n.setLocaleMessage(locale, translate.data);
        commit('setLocaleIsReady');
      }
      i18n.locale = locale;
      moment.locale(locale);
      dispatch('setLocaleToLS', locale);
      commit('setCurrentLocale', locale);
    },
    getLocaleFromLS: ({ state }, locale) => {
      if (!locale) {
        const localeFromLS = JSON.parse(localStorage.getItem('locale'));
        locale = localeFromLS?.version !== state.LSVersion ? 'ru' : localeFromLS.value;
      }
      return locale;
    },
    setLocaleToLS: ({ state }, locale) => {
      localStorage.setItem('locale', JSON.stringify({
        version: state.LSVersion,
        value: locale,
      }));
    },
  },
};
