<template>
  <div class="auth">
    <back-to-calendar-button @back-to-calendar="backToCalendar"></back-to-calendar-button>
    <h-card>
      <h-load-mask
          v-if="isLoading"
          absolute
          hide-ripple
          background-color="#ebebeb80"
      ></h-load-mask>
      <div class="auth-form-wrapper">
        <h3>{{ $t('enter.authTitleAuth') }}</h3>
        <h-form ref="form" >
          <h-text-field
              class="mt-3"
              :disabled="isLoading"
              v-model="login"
              required
              :label="$t('enter.login')"
          ></h-text-field>
          <h-text-field
              v-model="password"
              :label="$t('enter.password')"
              :disabled="isLoading"
              required
              type="password"
          ></h-text-field>
          <div class="auth__actions">
            <!--        <h-btn-->
            <!--          transparent-->
            <!--          @click="changeView"-->
            <!--        >-->
            <!--          {{ $t('enter.createAccount') }}-->
            <!--        </h-btn>-->
            <h-btn
                @click="tryAuthorize"
                :loading="isLoading"
            >
              {{ $t('enter.enter') }}
            </h-btn>
          </div>
        </h-form>
      </div>
    </h-card>
  </div>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import axios from 'axios';
import BackToCalendarButton from '../components/BackToCalendarButton.vue';

const auth = namespace('auth');

@Component({
  components: {
    BackToCalendarButton,
  },
})
class Auth extends Vue {
  @auth.Getter('authData')
  authData;

  @auth.Mutation('setAuthLogin')
  setAuthLogin;

  @auth.Mutation('setAuthPassword')
  setAuthPassword;

  @auth.Action('authorize')
  authorize;

  @auth.Mutation('clearInput')
  clearInput;

  isLoading = false;

  cancelToken = null;

  async tryAuthorize() {
    if (!this.$refs.form.checkValid()) return;
    this.isLoading = true;
    this.cancelToken = axios.CancelToken.source();
    await this.authorize(this.cancelToken.token);
    this.isLoading = false;
  }

  resetValid() {
    this.$refs.form.resetValid();
  }

  get login() {
    return this.authData.login;
  }

  set login(val) {
    this.setAuthLogin(val);
  }

  get password() {
    return this.authData.password;
  }

  set password(val) {
    this.setAuthPassword(val);
  }

  changeView() {
    this.$router.push({ name: 'Reg' });
  }

  backToCalendar() {
    if (this.cancelToken) {
      this.cancelToken.cancel();
      this.cancelToken = null;
    }
  }

  created() {
    this.clearInput();
  }
}

export default Auth;
</script>

