    right: 10px;
    top: 10px;
  }

  > .h-card {
    display: flex;
    flex-direction: column;
    max-width: calc(100% - 24px);
    width: 420px;
    padding: 0px;
    overflow: hidden;

    .auth-form-wrapper {
      padding: 8px;
    }

    .auth__actions {
      width: 100%;
      display: flex;
      //justify-content: space-between;
      justify-content: flex-end;
    }
  }
}
</style>
