<template>
  <div class="h-dialog" :class="classes">
    <transition name="h-dialog">
      <div
          v-show="value"
          class="h-dialog__wrapper"
          :style="wrapperStyles"
          ref="wrapper"
      >
        <h-card class="h-dialog__window" ref="content" :style="windowStyles">
          <div class="h-dialog__title">
            <slot name="title">
              <span>{{ title }}</span>
            </slot>
            <h-btn
                v-if="!hideCloseButton"
                @click="closeModal"
            >
              <span class="mdi mdi-close"></span>
            </h-btn>
          </div>
          <div class="h-dialog__content">
            <slot></slot>
          </div>
        </h-card>
      </div>
    </transition>
  </div>
</template>

<script>
import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import listenerBuilder from '../../../additional/ListenerBuilder';
import freeZIndexGetter from '../../../additional/FreeZIndexGetter';

const StartZIndex = 300;

@Component
class HDialog extends Vue {
  @Prop({
    type: Boolean,
    default: false,
  })
  value;

  @Prop()
  maxWidth;

  @Prop({
    type: Boolean,
    default: false,
  })
  persistent;

  @Prop({
    type: Boolean,
    default: false,
  })
  hideTitle;

  @Prop({
    type: [String, Number],
    default: '',
  })
  title;

  @Prop({
    type: Boolean,
    default: false,
  })
  hideCloseButton;

  zIndex = '';

  closeModal() {
    this.$emit('input', false);
    this.$emit('close');
  }

  @Watch('value')
  onChangeValue(val) {
    setTimeout(() => val ? this.onOpen() : this.onClose());
  }

  onOpen() {
    setTimeout(() => {
      this.zIndex = freeZIndexGetter('.h-dialog__wrapper', StartZIndex);
    });
  }

  onClose() {
    this.zIndex = '';
  }

  get windowStyles() {
    return {
      width: this.maxWidth ? `${this.maxWidth}px` : '',
    };
  }

  get wrapperStyles() {
    return {
      zIndex: this.zIndex,
    };
  }

  get classes() {
    return {
      'h-dialog--persistent': this.persistent,
      'h-dialog--active': this.value,
    };
  }

  addListeners() {
    this.removeListeners();
    listenerBuilder.addListener('mousedown', this.$refs.wrapper, (event) => {
      (this.value && event.target === this.$refs.wrapper) && this.closeModal();
    });
    listenerBuilder.addListener('vueClick', this.$refs.wrapper, (event) => {
      (this.value && event.target === this.$refs.wrapper) && this.closeModal();
    });
  }

  removeListeners() {
    listenerBuilder.removeListener('mousedown', this.$refs.wrapper);
    listenerBuilder.removeListener('vueClick', this.$refs.wrapper);
  }

  @Watch('persistent')
  onPersistentChange(val) {
    !val ? this.addListeners() : this.removeListeners();
  }

  mounted() {
    !this.persistent && this.addListeners();
    this.value && this.onOpen();
  }

  beforeDestroy() {
    this.removeListeners();
    this.value && this.onClose();
  }
}

export default HDialog;
</script>
