import Vue from 'vue';
import VueRouter from 'vue-router';
import ErrorPage from '../views/Error.vue';
import Auth from '../views/Auth.vue';
import Reg from '../views/Reg.vue';
import store from '../store';
import ManageSections from '../additional/SectionsGetter';
import loadMainModules from '../store/Main/mainModulesRegister';
import loadManageModules from '../store/Manage/manageModulesRegister';

Vue.use(VueRouter);

const routes = [
  {
    path: '*',
    name: 'ErrorPage',
    component: ErrorPage,
  },
  {
    path: '/',
    name: 'Main',
    component: () => import('../views/Main.vue'),
    beforeEnter: async (to, from, next) => {
      await loadMainModules();
      next();
    },
  },
  {
    path: '/auth',
    name: 'Auth',
    component: Auth,
    beforeEnter: (to, from, next) => {
      next(store.getters['user/haveUserData'] ? { name: 'Manage' } : undefined);
    },
  },
  {
    path: '/reg',
    name: 'Reg',
    component: Reg,
    redirect: { name: 'Auth' },
  },
  {
    path: '/manage',
    name: 'Manage',
    component: () => import('../views/Manage/Manage.vue'),
    beforeEnter: async (to, from, next) => {
      if (store.getters['user/haveUserData']) {
        await loadManageModules();
        next();
      } else {
        next({ name: 'Auth' });
      }
    },
    children: ManageSections,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
