<template>
  <button
      class="h-btn"
      :style="style"
      :class="classes"
      @click="onClick"
      :disabled="disabled || loading"
      v-wave
  >
    <div
        ref="content"
        class="h-btn__content"
    >
      <slot></slot>
    </div>

    <div
        v-show="loading"
        ref="loading"
        class="h-btn__loading"
    >
      <h-load-mask
          small
          absolute
          :color="style.color"
          background-color="rgba(0, 0, 0, 0)"
      ></h-load-mask>
    </div>
  </button>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
class HBtn extends Vue {
  @Prop({
    type: Boolean,
    default: false,
  })
  transparent;

  @Prop({
    type: String,
  })
  color;

  @Prop({
    type: Boolean,
    default: false,
  })
  small;

  @Prop({
    type: Boolean,
    default: false,
  })
  disabled;

  @Prop({
    type: Boolean,
    default: false,
  })
  loading;

  onClick() {
    this.$emit('click');
  }

  get bgColor() {
    if (this.transparent) {
      return 'transparent';
    }
    return this.color || '#02006b7a';
  }

  get style() {
    return {
      backgroundColor: this.bgColor,
      color: this.color || (this.transparent ? '#02006b99' : '#ffffff'),
    };
  }

  get classes() {
    return {
      'h-btn--small': this.small,
      'h-btn--transparent': this.transparent,
      'h-btn--loading': this.loading,
    };
  }
}

export default HBtn;
</script>
