<template>
  <div
      class="h-input h-select"
      :class="styles"
  >
    <div class="h-input__content">
      <fieldset>
        <legend ref="legend">
          <span>&#8203;</span>
        </legend>
      </fieldset>

      <h-menu
          v-model="isActive"
          activator-is-visible
          disable-activator
      >
        <template v-slot:activator>
          <div
              class="h-input__wrapper"
              @click.self.prevent="setActive"
              @mousedown.self.prevent="setLightning"
          >
            <div class="h-input__label" ref="label">{{ internalLabel }}</div>
            <input
                ref="input"
                class="h-input__text-field"
                v-model="selectedItemText"
                @click="setActive"
                @focus="setLightning"
                @keydown.tab="unsetActive"
                @keydown.esc="unsetActive"
                @keydown.enter="setActive"
                readonly
            >
            <span
                v-show="clearButtonIsVisible"
                :disabled="disabled"
                class="h-input__clear-button mdi mdi-close"
                @click.stop="clearInternalValue"
            ></span>
          </div>
        </template>
        <template>
          <h-select-menu
              ref="select-menu"
              :items="internalItems"
              :value="internalValue"
              :item-text="itemText"
              @on-select-item="selectItem"
          ></h-select-menu>
        </template>
      </h-menu>
    </div>
    <span
        v-if="!hideMessage"
        class="h-input__message"
    >{{ message }}</span>
  </div>
</template>

<script>
import { Component, Prop, Watch } from 'vue-property-decorator';
import HInput from '../h-input/HInput.vue';
import HSelectMenu from '../h-select-menu/HSelectMenu.vue';

@Component({
  components: {
    HSelectMenu,
  },
})
class HSelect extends HInput {
  @Prop({
    type: Array,
    default: () => [],
  })
  items;

  @Prop({
    type: [Array, Object, String, Number],
    default: undefined,
  })
  value;

  @Prop({
    type: String,
    default: undefined,
  })
  itemValue;

  @Prop({
    type: String,
    default: undefined,
  })
  itemText;

  @Prop({
    type: Boolean,
    default: false,
  })
  returnObject;

  @Prop({
    type: Boolean,
    default: false,
  })
  multiple;

  @Watch('isActive')
  onChangeIsActive(val) {
    if (val) {
      this.$refs['select-menu'].$el.style.width = `${this.$el.clientWidth}px`;
    }
  }

  clearInternalValue() {
    this.internalValue = [];
  }

  get internalItems() {
    const internalItems = {};
    this.items.forEach(item => {
      internalItems[this.itemValue ? item[this.itemValue] : item] = item;
    });
    return internalItems;
  }

  get haveValue() {
    return !!Object.keys(this.internalValue).length;
  }

  clearValue() {
    this.internalValue = [];
  }

  selectItem(key) {
    let internalValue = JSON.parse(JSON.stringify(this.internalValue));
    if (!this.multiple) {
      internalValue = {
        [key]: this.internalItems[key],
      };
      this.unsetActive();
    } else {
      key in internalValue ? delete internalValue[key] : internalValue[key] = this.internalItems[key];
    }
    this.internalValue = Object.values(internalValue);
  }

  get internalValue() {
    const internalValue = {};
    if (![undefined, null].includes(this.value)) {
      const _value = this.multiple ? this.value : [this.value];
      _value.forEach(item => {
        const _key = this.returnObject ? item[this.itemValue] : item;
        internalValue[_key] = this.internalItems[_key];
      });
    }
    return internalValue;
  }

  set internalValue(value) {
    const _value = value.map(item => (this.returnObject && typeof item === 'object' ? item : item[this.itemValue] || item));
    this.$emit('input', this.multiple ? _value : _value[0]);
  }

  get selectedItemText() {
    const value = Object.values(this.internalValue);
    let selectedItemText = '';
    if (value.length === this.items.length) {
      selectedItemText = this.$t('h-select.all');
    } else if (value.length > 1) {
      selectedItemText = this.$t('h-select.several');
    } else {
      selectedItemText = this.itemText && typeof value[0] === 'object' ? value[0][this.itemText] : value[0];
    }
    return selectedItemText;
  }

  get labelIsTransformed() {
    return this.haveValue;
  }
}

export default HSelect;
</script>
