const sizeMap = {
  pc: [960],
  tablet: [600, 959],
  mobile: [0, 599],
};

export default {
  namespaced: true,
  state: {
    pc: false,
    tablet: false,
    mobile: false,
  },
  getters: {
    pc: state => state.pc,
    tablet: state => state.tablet,
    mobile: state => state.mobile,
    smallSize: state => state.mobile || state.tablet,
  },
  mutations: {
    setResolutionValues: (state, payload) => {
      Object.keys(payload).forEach(resolution => state[resolution] = payload[resolution]);
    },
  },
  actions: {
    setResolutionValues: ({ commit }, currentWidth) => {
      commit('setResolutionValues', {
        pc: sizeMap.pc[0] <= currentWidth,
        tablet: sizeMap.tablet[0] <= currentWidth && currentWidth <= sizeMap.tablet[1],
        mobile: sizeMap.mobile[0] <= currentWidth && currentWidth <= sizeMap.mobile[1],
      });
    },
  },
};
