<template>
  <div
      ref="menu"
      class="h-menu h-select__menu"
  >
    <span v-if="!haveItems"> </span>
    <div
        v-else
        class="h-select__items-wrapper"
        ref="items-wrapper"
        @scroll="onMenuScroll"
    >
      <div
          class="h-select__items-line"
          ref="items-line"
      >
        <div
            v-for="(item, key) in visibleItems"
            :key="key"
            class="h-select__menu-item"
            @click="selectItem(key)"
            :class="{ 'h-select__menu-item--selected' : itemIsSelected(key) }"
            v-wave
        >
          {{ getItemText(item) }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
class HSelectMenu extends Vue {
  @Prop({
    type: Object,
    default: () => {
      return {};
    },
  })
  items;

  @Prop({
    type: Object,
    default: () => {
      return {};
    },
  })
  value;

  @Prop({
    type: String,
    default: undefined,
  })
  itemText;

  selectItem(key) {
    this.$emit('on-select-item', key);
  }

  itemIsSelected(key) {
    return !!(key in this.value);
  }

  get haveItems() {
    return !!Object.keys(this.items).length;
  }

  getItemText(item) {
    return this.itemText && typeof item === 'object' ? item[this.itemText] : item;
  }

  visibleItemsStep = 20;

  visibleItemsCount = this.visibleItemsStep;

  extendVisibleItems() {
    this.visibleItemsCount += this.visibleItemsStep;
  }

  get visibleItems() {
    const visibleItems = {};
    Object.keys(this.items).forEach((key, index) => {
      if (index < this.visibleItemsCount) {
        visibleItems[key] = this.items[key];
      } else return;
    });
    return visibleItems;
  }

  get menuHeight() {
    return this.$refs.menu.clientHeight;
  }

  onMenuScroll() {
    window.requestAnimationFrame(() => {
      if (this.$refs['items-line'].clientHeight - this.menuHeight === this.$refs['items-wrapper'].scrollTop) {
        this.extendVisibleItems();
      }
    });
  }
}

export default HSelectMenu;
</script>
