import Vue from 'vue';
import Vuex from 'vuex';
import locale from './locale';
import user from './user';
import auth from './auth';
import reg from './reg';
import resolution from './resolution';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    appIsLoading: false,
  },
  mutations: {
    setAppLoading: (state) => state.appIsLoading = true,
    unsetAppLoading: (state) => state.appIsLoading = false,
  },
  actions: {},
  modules: {
    locale,
    user,
    auth,
    reg,
    resolution,
  },
});

export default store;
