<template>
  <div
      class="h-load-mask"
      :class="styles"
      :style="{ 'backgroundColor' : backgroundColor }"
  >
    <div class="h-load-mask__ripple" v-if="!hideRipple">
      <div :style="{ 'backgroundColor': color }"></div>
      <div :style="{ 'backgroundColor': color }"></div>
    </div>
  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
class HLoadMask extends Vue {
  @Prop({
    type: Boolean,
    default: false,
  })
  absolute;

  @Prop({
    type: Boolean,
    default: false,
  })
  small;

  @Prop({
    type: String,
    default: '',
  })
  backgroundColor;

  @Prop({
    type: String,
    default: '',
  })
  color;

  @Prop({
    type: Boolean,
    default: false,
  })
  hideRipple;

  get styles() {
    return {
      'h-load-mask--absolute': this.absolute,
      'h-load-mask--small': this.small,
    };
  }
}

export default HLoadMask;
</script>
