<template>
  <form class="h-form" onsubmit="return false" novalidate="novalidate" autocomplete="off">
    <slot></slot>
  </form>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
class HForm extends Vue {
  @Prop({
    type: Boolean,
    default: false,
  })
  value;

  checkValid() {
    this.$children.forEach(child => {
      if ('setCheckValid' in child) {
        child.setCheckValid();
      }
    });
    return !this.$children.some(child => ('isValid' in child) ? !child.isValid : false);
  }

  resetValid() {
    this.$children.forEach(child => {
      if ('unsetCheckValid' in child) {
        child.unsetCheckValid();
        child.clearValue();
      }
    });
  }
}

export default HForm;
</script>
