<template>
  <div
      class="h-input h-text-field"
      :class="styles"
  >
    <div class="h-input__content">
      <fieldset>
        <legend ref="legend">
          <span>&#8203;</span>
        </legend>
      </fieldset>

      <div
          class="h-input__wrapper"
          @mousedown.self.prevent="setActive"
      >
        <div class="h-input__label" ref="label">{{ internalLabel }}</div>
        <input
            ref="input"
            class="h-input__text-field"
            v-model="internalValue"
            :type="type"
            @focus="setActive"
            @keydown.tab="unsetActive"
            @keydown.esc="unsetActive"
            :disabled="disabled"
        >
        <span
            :disabled="disabled"
            v-show="clearButtonIsVisible"
            class="h-input__clear-button mdi mdi-close"
            @click.self="clearInternalValue"
        ></span>
      </div>
    </div>
    <span
        v-if="!hideMessage"
        class="h-input__message"
    >{{ message }}</span>
  </div>
</template>

<script>
import { Component, Prop } from 'vue-property-decorator';
import HInput from '../h-input/HInput.vue';

@Component
class HTextField extends HInput {
  @Prop({
    type: [String, Number],
    default: '',
  })
  value;

  @Prop({
    type: String,
    default: 'text',
  })
  type;

  get internalValue() {
    return this.value;
  }

  set internalValue(val) {
    this.$emit('input', val);
  }

  clearInternalValue() {
    if (this.isActive) this.focus();
    this.internalValue = '';
  }

  get haveValue() {
    return ![undefined, null, ''].includes(this.internalValue);
  }

  get labelIsTransformed() {
    return this.isActive || this.haveValue;
  }

  clearValue() {
    this.internalValue = undefined;
  }
}

export default HTextField;
</script>
