import store from './index';

async function moduleLoader(moduleName = '', submodules = []) {
  const mainModules = await Promise.all(submodules.map(sm => {
    return import(`./${moduleName[0].toUpperCase()}${moduleName.slice(1)}/${sm}`);
  }));
  const modules = {};
  mainModules.forEach(m => {
    modules[m.default.moduleName] = m.default;
  });
  store.registerModule(`${moduleName}-module`, {
    namespaced: true,
    modules,
  });
}

export default moduleLoader;
