<template>
  <div class="auth">
    <back-to-calendar-button></back-to-calendar-button>
    <h-card>
      <h3>{{ $t('enter.authTitleReg') }}</h3>
      <h-text-field
          class="mt-3"
          v-model="login"
          :label="$t('enter.login')"
          required
      ></h-text-field>
      <h-text-field
          v-model="password"
          :label="$t('enter.password')"
          required
          type="password"
      ></h-text-field>
      <h-text-field
          v-model="bio"
          :label="$t('enter.bio')"
          required
      ></h-text-field>
      <div class="auth__actions">
        <h-btn
            transparent
            @click="changeView"
        >
          {{ $t('enter.enterInAccount') }}
        </h-btn>
        <h-btn
            @click="register"
        >
          {{ $t('enter.create') }}
        </h-btn>
      </div>
    </h-card>
  </div>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import BackToCalendarButton from '../components/BackToCalendarButton.vue';

const reg = namespace('reg');

@Component({
  components: {
    BackToCalendarButton,
  },
})
class Auth extends Vue {
  @reg.Getter('regData')
  regData;

  @reg.Mutation('setRegLogin')
  setRegLogin;

  @reg.Mutation('setRegPassword')
  setRegPassword;

  @reg.Mutation('setRegBio')
  setRegBio;

  @reg.Action('register')
  register;

  @reg.Mutation('clearInput')
  clearInput;

  get login() {
    return this.regData.login;
  }

  set login(val) {
    this.setRegLogin(val);
  }

  get password() {
    return this.regData.password;
  }

  set password(val) {
    this.setRegPassword(val);
  }

  get bio() {
    return this.regData.bio;
  }

  set bio(val) {
    this.setRegBio(val);
  }

  changeView() {
    this.$router.push({ name: 'Auth' });
  }

  backToCalendar() {
    this.$router.push({ name: 'Main' });
  }

  created() {
    this.clearInput();
  }
}

export default Auth;
</script>

<style lang="scss" scoped>
.auth {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  .back-to-calendar-button {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  > .h-card {
    display: flex;
    flex-direction: column;
    max-width: calc(100% - 24px);
    width: 420px;

    .auth__actions {
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
  }
}
</style>
