import router from '../router';
import $requests from '../requests';

export default {
  namespaced: true,
  state: {
    authData: {
      login: undefined,
      password: undefined,
    },
  },
  getters: {
    authData: (state) => state.authData,
  },
  mutations: {
    setAuthLogin: (state, login) => state.authData.login = login,
    setAuthPassword: (state, password) => state.authData.password = password,
    clearInput: (state) => {
      state.authData.login = '';
      state.authData.password = '';
    },
  },
  actions: {
    authorize: async ({ state, commit }, cancelToken) => {
      const response = await $requests.auth(JSON.stringify({
        username: state.authData.login,
        password: state.authData.password,
      }), cancelToken);
      if (response.success) {
        router.push({ name: 'Main' });
        commit('user/setUserData', response.data.result, { root: true });
      } else {
        alert(response.data.error);
      }
    },
    // authorize: async () => {
    //   return new Promise(resolve => {
    //     setTimeout(() => {
    //       resolve();
    //     }, 10000);
    //   });
    // },
  },
};
