import axios from 'axios';

const $requests = {
  url: process.env.VUE_APP_URL ? `http://${process.env.VUE_APP_URL}` : '',
  async request({
    method = 'get',
    url = this.url,
    endPoint,
    data = undefined,
    cancelToken = undefined,
  }) {
    try {
      const response = await axios({
        method,
        url: `${url}/${endPoint}`,
        data,
        cancelToken,
      });
      if (!response.data.error) {
        return {
          success: true,
          data: response.data,
        };
      }
    } catch (e) {
      return {
        success: false,
        data: e.response?.data,
      };
    }
  },
  eventsCancelToken: null,
  async events(city, year, month) {
    if (this.eventsCancelToken) this.eventsCancelToken.cancel();
    this.eventsCancelToken = axios.CancelToken.source();
    return this.request({ endPoint: `events/${city}/${year}/${month + 1}`, cancelToken: this.eventsCancelToken.token });
  },
  async tags() {
    return this.request({ endPoint: 'tags' });
  },
  async cities() {
    return this.request({ endPoint: 'cities' });
  },
  async createUser(data) {
    return this.request({ method: 'post', endPoint: 'users', data });
  },
  async auth(data, cancelToken) {
    return this.request({ method: 'post', endPoint: 'auth', data, cancelToken });
  },
};

export default $requests;
