import store from '../store';

/**
 *     (   )
 * */
const Sections = ['cities', 'tags'];

const ManageSections = [];

class Section {
  constructor(sectionName) {
    this._sourceName = sectionName;
    this.name = this.getNameTranslate(sectionName);
    this.path = `/manage/${sectionName}`;
    this.component = this.getComponentPath.bind(this);
  }

  getNameTranslate(name) {
    return `manage.sectionsList.${name}`;
  }

  getComponentPath() {
    return import(`../views/Manage/${this._sourceName[0].toUpperCase()}${this._sourceName.slice(1)}.vue`);
  }

  beforeEnter(to, from, next) {
    store.commit('manage-module/main/setActiveManageSection', to.fullPath, { root: true });
    next();
  }
}

Sections.forEach(sectionName => {
  const sectionElement = new Section(sectionName);
  ManageSections.push(sectionElement);
});

export default ManageSections;
