import listenerOutsideBuilder from '../additional/ListenerBuilder';

export default {
  bind(el, binding) {
    listenerOutsideBuilder.addListener('mousedown', el, (event) => {
      if (binding.value && ![...event.path].includes(el)) {
        binding.value();
      }
    });
  },
  unbind(el) {
    listenerOutsideBuilder.removeListener('mousedown', el);
  },
};
