import router from '../router';
import $requests from '../requests';

export default {
  namespaced: true,
  state: {
    regData: {
      login: undefined,
      password: undefined,
      bio: undefined,
    },
  },
  getters: {
    regData: (state) => state.regData,
  },
  mutations: {
    setRegLogin: (state, login) => state.regData.login = login,
    setRegPassword: (state, password) => state.regData.password = password,
    setRegBio: (state, bio) => state.regData.bio = bio,
    clearInput: (state) => {
      state.regData.login = '';
      state.regData.password = '';
      state.regData.bio = '';
    },
  },
  actions: {
    register: async ({ state, commit }) => {
      const response = await $requests.createUser(JSON.stringify({
        username: state.regData.login,
        password: state.regData.password,
        bio: state.regData.bio,
        avatar: '',
      }));
      if (response.success) {
        router.push({ name: 'Main' });
        commit('user/setUserData', response.data, { root: true });
      }
    },
  },
};
