<template>
  <div
      class="h-input h-autocomplete"
      :class="styles"
  >
    <div class="h-input__content">
      <fieldset>
        <legend ref="legend">
          <span>&#8203;</span>
        </legend>
      </fieldset>

      <h-menu
          v-model="isActive"
          activator-is-visible
          disable-activator
      >
        <template v-slot:activator>
          <div
              class="h-input__wrapper"
              @click.self.prevent="setActive"
              @mousedown.self.prevent="setLightning"
          >
            <div class="h-input__label" ref="label">{{ internalLabel }}</div>
            <input
                ref="input"
                class="h-input__text-field"
                v-model="mergedText"
                @click="setActive"
                @focus="setLightning"
                @keydown.tab="unsetActive"
                @keydown.esc="unsetActive"
                @keydown.enter="setActive"
                @keydown="onTextFieldKeydown"
            >
            <span
                v-show="clearButtonIsVisible"
                :disabled="disabled"
                class="h-input__clear-button mdi mdi-close"
                @click.stop="clearInternalValue"
            ></span>
          </div>
        </template>
        <template>
          <h-select-menu
              ref="select-menu"
              :items="internalItems"
              :value="internalValue"
              :item-text="itemText"
              @on-select-item="selectItem"
          ></h-select-menu>
        </template>
      </h-menu>
    </div>
    <span
        v-if="!hideMessage"
        class="h-input__message"
    >{{ message }}</span>
  </div>
</template>

<script>
import { Component, Watch } from 'vue-property-decorator';
import { mixins } from 'vue-class-component';
import HSelectMenu from '../h-select-menu/HSelectMenu.vue';
import HSelect from '../h-select/HSelect.vue';
import HTextField from '../h-text-field/HTextField.vue';

@Component({
  components: {
    HSelectMenu,
  },
})
class HAutocomplete extends mixins(HTextField, HSelect) {
  searchString = '';

  filterItemsBySearchString = false;

  @Watch('isActive')
  changeIsActive(val) {
    if (this.$refs.input) {
      if (!val) {
        if (this.haveValue) {
          this.$refs.input.selectionEnd = this.$refs.input.selectionStart;
        }
        this.filterItemsBySearchString = false;
        this.searchString = '';
        this.blur();
      } else {
        this.focus();
      }
    }
  }

  @Watch('internalValue')
  onChangeInternalValue() {
    if (this.isActive) {
      this.focus();
    }
  }

  set mergedText(val) {
    this.filterItemsBySearchString = true;
    this.searchString = val;
  }

  get mergedText() {
    return this.filterItemsBySearchString ? this.searchString : this.selectedItemText;
  }

  get haveMerged() {
    return ![undefined, null, ''].includes(this.mergedText);
  }

  get internalItems() {
    const internalItems = {};
    this.items.forEach(item => {
      if (this.filterItemsBySearchString) {
        if (this.itemValue && String(item[this.itemText]).includes(this.searchString)) {
          internalItems[item[this.itemValue]] = item;
        } else if (String(item).includes(this.searchString)) {
          internalItems[item] = item;
        }
      } else {
        internalItems[this.itemValue ? item[this.itemValue] : item] = item;
      }
    });
    return internalItems;
  }

  onTextFieldKeydown(event) {
    if (!this.mergedText && event.code === 'Backspace') {
      this.internalValue = [];
    }
  }

  @Watch('isLightning')
  onIsLightningChange() {
    this.setLegendWidth();
  }

  get labelIsTransformed() {
    return this.isActive || this.isLightning || this.haveMerged || this.haveValue;
  }
}

export default HAutocomplete;
</script>
