<template>
  <div class="h-table h-card">
    <table v-if="!isMobile">
      <thead>
      <tr class="h-table__header">

        <th
            v-for="(hItem, index) in headers"
            :key="index"
            :width="hItem.width"
            :align="hItem.align"
        >
          <slot name="header" :header="hItem">{{ hItem.text }}</slot>
        </th>
      </tr>
      </thead>
      <tbody>
      <tr
          class="h-table__items-row"
          v-for="(item, index) in items"
          :key="index"
      >
        <slot name="item" :item="item">
          <td
              v-for="(hItem, hIndex) in headers"
              :key="hIndex"
          >
            {{ item[hItem.value] }}
          </td>
        </slot>
      </tr>
      </tbody>
    </table>
    <div v-else class="h-table--mobile">
      <div
          class="h-table--mobile__item"
          v-for="(item, index) in items"
          :key="index"
      >
        <slot name="mobile-title-action" :item-data="{ item, index }"></slot>
        <div
            class="h-table--mobile__field"
            v-for="(hItem, hIndex) in internalMobileHeaders"
            :key="hIndex"
        >
          <slot name="mobile-item" :item="item">
            <span class="h-table--mobile__field-name">{{ hItem.text }}</span>
            <span class="h-table--mobile__field-value">{{ item[hItem.value] }}</span>
          </slot>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { Vue, Component, Prop } from 'vue-property-decorator';
import listenedBuilder from '../../../additional/ListenerBuilder';

@Component
class HTable extends Vue {
  @Prop({
    type: Array,
    default: () => ([]),
  })
  headers;

  @Prop({
    type: Array,
    default: () => ([]),
  })
  mobileHeaders;

  @Prop({
    type: Array,
    default: () => ([]),
  })
  items;

  @Prop({
    type: Number,
    default: 959,
  })
  mobileWidth;

  isMobile = false;

  setIsMobile() {
    this.isMobile = document.querySelector('#app').clientWidth <= this.mobileWidth;
  }

  created() {
    this.setIsMobile();
    listenedBuilder.addListener('resize', this.$el, this.setIsMobile);
  }

  beforeDestroy() {
    listenedBuilder.removeListener('resize', this.$el);
  }

  get internalMobileHeaders() {
    return this.mobileHeaders.length ? this.mobileHeaders : this.headers;
  }
}

export default HTable;
</script>

<style lang="scss">
.h-table {
  height: 200px;
}
</style>
