<template>
  <h-btn
      class="back-to-calendar-button"
      @click="backToCalendar"
      transparent
  >{{ $t('enter.backToCalendar') }}
  </h-btn>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';

@Component
class BackToCalendarButton extends Vue {
  backToCalendar() {
    this.$emit('back-to-calendar');
    this.$router.push({ name: 'Main' });
  }
}

export default BackToCalendarButton;
</script>
