import router from '../router';

export default {
  namespaced: true,
  state: {
    userData: {
      // avatart: '',
      // createdAt: '2021-05-27T15:16:50.205348Z',
      // id: 14,
      // updatedAt: '2021-05-27T15:16:50.205348Z',
      // username: 'test',
      // _allowedCities: [1],
    },
  },
  getters: {
    userData: state => state.userData,
    haveUserData: state => Object.values(state.userData).length,
  },
  mutations: {
    setUserData: (state, userData) => state.userData = userData,
    clearUserData: state => state.userData = {},
  },
  actions: {
    logout: ({ commit }) => {
      commit('clearUserData');
      router.currentRoute.name !== 'Main' && router.push({ name: 'Main' });
    },
  },
};
