<script>
import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import listenerBuilder from '../../../additional/ListenerBuilder';

@Component
class HInput extends Vue {
  @Prop({
    type: [String, Function],
    default: '',
  })
  label;

  @Prop({
    type: Boolean,
    default: false,
  })
  small;

  @Prop({
    type: Boolean,
    default: true,
  })
  showClearButton;

  @Prop({
    type: Array,
    default: () => [],
  })
  rules;

  @Prop({
    type: Boolean,
    default: false,
  })
  required;

  @Prop({
    type: Boolean,
    default: false,
  })
  hideMessage;

  @Prop({
    type: Boolean,
    default: false,
  })
  disabled;

  @Prop({
    type: Boolean,
    default: false,
  })
  autofocus;

  message = '';

  checkValid = false;

  setCheckValid() {
    this.checkValid = true;
  }

  unsetCheckValid() {
    this.checkValid = false;
  }

  get isValid() {
    this.message = '';
    if (!this.checkValid) return true;
    if (this.required && !this.haveValue) return false;
    if (!this.rules.length) return true;
    return !this.rules.some(rule => {
      if (typeof rule === 'boolean') {
        return !rule;
      } else {
        const ruleResult = rule(this.value);
        if (typeof ruleResult === 'string') {
          this.message = ruleResult;
          return true;
        }
        return !ruleResult;
      }
    });
  }

  get internalLabel() {
    return typeof this.label === 'string' ? this.label : this.label();
  }

  isActive = false;

  isLightning = false;

  setActive() {
    if (this.disabled) return;
    this.isActive = true;
  }

  unsetActive() {
    this.isActive = false;
    this.unsetLightning();
  }

  get haveValue() {
    return false;
  }

  get clearButtonIsVisible() {
    return this.showClearButton && this.haveValue;
  }

  @Watch('haveValue')
  onChangeHaveValue() {
    this.setLegendWidth();
  }

  @Watch('label')
  onLabelChange() {
    this.setLegendWidth();
  }

  @Watch('isActive')
  changeOnIsActive(val) {
    if (this.$refs.input) {
      if (!val) {
        if (this.haveValue) this.$refs.input.selectionEnd = this.$refs.input.selectionStart;
        this.blur();
        this.setCheckValid();
      } else {
        this.focus();
      }
    }
    this.setLegendWidth();
  }

  mounted() {
    listenerBuilder.addListener('vueClick', this.$el, (event) => {
      ((this.isActive || this.isLightning) && !event.composedPath().includes(this.$el)) && this.unsetActive();
    });
    listenerBuilder.addListener('mousedown', this.$el, (event) => {
      ((this.isActive || this.isLightning) && !event.composedPath().includes(this.$el)) && this.unsetActive();
    });
    this.setLegendWidth();
    this.autofocus && this.setActive();
  }

  beforeDestroy() {
    listenerBuilder.removeListener('vueClick', this.$el);
    listenerBuilder.removeListener('mousedown', this.$el);
  }

  focus() {
    this.$refs.input.focus();
  }

  blur() {
    this.$refs.input.blur();
  }

  get labelIsTransformed() {
    return false;
  }

  setLegendWidth() {
    if (this.$refs.legend && (!this.labelIsTransformed || !this.label)) {
      this.$refs.legend.style.width = '0px';
    } else {
      this.$refs.legend.style.width = `${(this.$refs.label.clientWidth + 9) * 0.75}px`;
    }
  }

  setLightning() {
    this.isLightning = true;
  }

  unsetLightning() {
    this.isLightning = false;
  }

  get styles() {
    return {
      'h-input--transformed-label': this.labelIsTransformed,
      'h-input--active-lightning': this.isLightning || this.isActive,
      'h-input--have-clear-button': this.showClearButton,
      'h-input--invalid': !this.isValid,
      'h-input--disabled': this.disabled,
      'h-input--small': this.small,
    };
  }
}

export default HInput;
</script>
