<template>
  <div id="app" class="test-app" @click="onVueClick">
    <div class="menu-wrapper">
      <div class="h-table-wrapper">
        <h-btn
            class="h-btn--1"
          @click="modal=true"
        >
          open modal
        </h-btn>
        <h-dialog
          v-model="modal"
        >
          modal content
        </h-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import listenerBuilder from './additional/ListenerBuilder';

const locale = namespace('locale');
const resolution = namespace('resolution');

@Component
class TestApp extends Vue {
  model = {
    value: 'a',
    text: 'first',
    width: '30%',
    align: 'left',
  };

  @locale.Action('setLocale')
  setLocale;

  onVueClick(event) {
    listenerBuilder.onVueClick(event);
  }

  @resolution.Action('setResolutionValues')
  setResolutionValues;

  modal = false;

  headers = [
    {
      value: 'a',
      text: 'first',
      width: '30%',
      align: 'left',
    },
    {
      value: 'b',
      text: 'second',
      width: '40%',
      align: 'left',
    },
    {
      value: 'c',
      text: 'third',
      width: '30%',
      align: 'left',
    },
  ];

  items = [
    {
      a: 13,
      b: 14,
      c: 15,
    },
    {
      a: 14,
      b: 15,
      c: 16,
    },
    {
      a: 15,
      b: 16,
      c: 17,
    },
    {
      a: 16,
      b: 17,
      c: 18,
    },
    {
      a: 13,
      b: 14,
      c: 15,
    },
    {
      a: 14,
      b: 15,
      c: 16,
    },
    {
      a: 13,
      b: 14,
      c: 15,
    },
    {
      a: 14,
      b: 15,
      c: 16,
    },
    {
      a: 13,
      b: 14,
      c: 15,
    },
    {
      a: 14,
      b: 15,
      c: 16,
    },
  ]

  created() {
    this.setLocale();
  }

  onResize(event) {
    this.setResolutionValues(event.currentTarget.innerWidth);
    this.setHeight();
  }

  setHeight() {
    const doc = document.documentElement;
    doc.style.setProperty('--app-height', `${window.innerHeight}px`);
  }

  mounted() {
    listenerBuilder.addListener('resize', this.$el, this.onResize);
    this.setResolutionValues(document.body.clientWidth);
    this.setHeight();
  }
}

export default TestApp;
</script>

<style lang="scss">
.h-table-wrapper {
  height: 200vh;
}

.h-btn--1 {
  margin-top: 50vh;
}
</style>
